<template>
    <div class="demo">
      <el-button @click="test">watch</el-button>
      {{obj}}
    </div>
</template>

<script>
import demoMixin from './demoMixin'
import 'kar'
export default {
  name: 'demo',
  mixins: [demoMixin],
  data () {
    return {
      msg: 'hello, zr',
      obj: {time: ''}
    }
  },
  computed: {
    newMsg () {
      return this.msg + 'new'
    }
  },
  methods: {
    test () {
      this.msg = new Date().getTime()
      console.log(this.newMsg)
    }
  },
  destroyed () {
  },
  mounted () {
    var fun
    console.log(fun)
    function fun () {
      return 'hello'
    }
    console.log(fun)
    fun = 3
    console.log(fun)
    function fun () {
      return 'hello'
    }
    console.log(fun, fun())
  }
}
</script>

<style scoped lang="scss">
</style>



